import React, { Component } from "react";
import "./mapHeatChart.scss";
import { SVGMap } from "react-svg-map";
import "react-svg-map/lib/index.css";
import SaudiArabia from "../../../assets/saudi-arabia";
import localNames from "../../../assets/saudi-arabia/localNames.json";

export default class mapHeatChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pointedLocation: null,
      tooltipStyle: {
        display: "none"
      }
    };
    this.getLocationClassName = this.getLocationClassName.bind(this);
    this.handleLocationMouseOver = this.handleLocationMouseOver.bind(this);
    this.handleLocationMouseOut = this.handleLocationMouseOut.bind(this);
    this.handleLocationMouseMove = this.handleLocationMouseMove.bind(this);
  }
  handleLocationMouseOver(event) {
    event.persist();
    const { cities } = this.props;
    let rgn = event.target.attributes.id.value;
    let region = localNames.Regions[rgn];
    let count = 0;
    if (cities[rgn]) {
      Object.keys(cities[rgn]).forEach(city => {
        count += parseInt(cities[rgn][city]);
      });
    }
    this.props.hover(rgn);
    this.setState({
      pointedLocation: `${region.English}: ${count}`
    });
  }

  handleLocationMouseOut() {
    this.setState({
      pointedLocation: null,
      tooltipStyle: { display: "none" }
    });
  }

  handleLocationMouseMove(event) {
    const tooltipStyle = {
      display: "block",
      top: event.clientY + 10,
      left: event.clientX - 100
    };
    this.setState({ tooltipStyle });
  }

  getLocationClassName(location, index) {
    // Generate random heat map
    //location : id,name
    const { cities, total } = this.props;
    let values = cities[location.id];
    let count = 0;
    if (values) {
      Object.keys(values).forEach(value => {
        count += parseInt(values[value]);
      });
      let percent = (count * 100) / total;
      return `svg-map__location svg-map__location--heat${
        percent > 75
          ? 3
          : percent > 50
          ? 2
          : percent > 25 || percent > 0
          ? 1
          : 0
      }`;
    }
    return `svg-map__location svg-map__location--heat0`;
  }

  render() {
    return (
      <article className="heatmap__block">
        <div className="heatmap__block__map heatmap__block__map--sa">
          <SVGMap
            map={SaudiArabia}
            locationClassName={this.getLocationClassName}
            onLocationMouseOver={this.handleLocationMouseOver}
            onLocationMouseOut={this.handleLocationMouseOut}
            onLocationMouseMove={this.handleLocationMouseMove}
          />
          <div
            className="heatmap__block__map__tooltip"
            style={this.state.tooltipStyle}
          >
            {this.state.pointedLocation}
          </div>
        </div>
      </article>
    );
  }
}
