import React, { Component } from "react";
import "./dataInput.scss";
import { Redirect } from "react-router-dom";
import SourceService from "../../services/sourceServices";
import localNames from "../../assets/saudi-arabia/localNames.json";
import CityInput from "../elements/cityInput";
export default class dataInput extends Component {
  state = {
    ready: false,
    confirmed: 0,
    intensive: 0,
    hospital: 0,
    recovered: 0,
    deaths: 0,
    nationality: {
      in: 0,
      out: 0
    },
    source: {
      local: 0,
      import: 0
    },
    area: "none",
    city: {}
  };
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    SourceService.getCases(2).then(data => {
      this.setState({
        confirmed: data.confirmed,
        intensive: data.intensive,
        hospital: data.hospital,
        nationality: {
          in: data.nationality.in,
          out: data.nationality.out
        },
        source: {
          local: data.source.local,
          import: data.source.import
        },
        city: data.city
      });
      this.setState({ ready: true });
    });
  }
  updateValue(val, name, e) {
    val[name] = e.target.value;
    this.setState({ val });
  }
  handleChange = e => {
    e.persist();
    if (e.target.className.includes("city")) {
      this.setState({
        ...this.state,
        city: {
          ...this.state.city,
          [e.target.dataset.region]: {
            ...this.state.city[e.target.dataset.region],
            [e.target.dataset.id]: e.target.value
          }
        }
      });
    }
  };
  handleSubmit(event) {
    event.preventDefault();
    localStorage.removeItem("Auth");
    let data = this.state;
    event.persist();
    delete data["val"];
    SourceService.setCases(data);
    this.forceUpdate();
  }
  formatCities() {
    let cities = [];
    let localCities = localNames.Regions[this.state.area].Cities;
    Object.keys(localCities).map((city, ci) => {
      return cities.push({
        id: city,
        name: localCities[city].English,
        value: this.state.city[this.state.area]
          ? this.state.city[this.state.area][city] !== undefined
            ? this.state.city[this.state.area][city]
            : 0
          : 0
      });
    });
    return cities;
  }
  render() {
    let auth = localStorage.getItem("Auth");
    if (!auth) return <Redirect to="/login"></Redirect>;
    if (this.state.ready === true)
      return (
        <div id="dashboard" className="container">
          <div className="columns flex-centered my-2">
            <div style={{ width: "70%" }}>
              <form onSubmit={this.handleSubmit} onChange={this.handleChange}>
                <div className="d-flex justify-content-center">
                  <h1 className="mainColor">COVID Data Update</h1>
                </div>
                <hr></hr>
                <h4>Cases</h4>
                <div className="form-row">
                  <div className="col-md-3 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      confirmed cases
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.confirmed}
                      onChange={e =>
                        this.updateValue(this.state, "confirmed", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>

                  <div className="col-md-3 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      hospitalized cases
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.hospital}
                      onChange={e =>
                        this.updateValue(this.state, "hospital", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-3 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      in intensive care
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.intensive}
                      onChange={e =>
                        this.updateValue(this.state, "intensive", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      recovered cases
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.recovered}
                      onChange={e =>
                        this.updateValue(this.state, "recovered", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-5 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      deaths
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.deaths}
                      onChange={e => this.updateValue(this.state, "deaths", e)}
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Nationality</h4>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Saudi Arabian
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.nationality.in}
                      onChange={e =>
                        this.updateValue(this.state.nationality, "in", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Other Nationality
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.nationality.out}
                      onChange={e =>
                        this.updateValue(this.state.nationality, "out", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Transmission Source</h4>
                <div className="form-row">
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Local
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.source.local}
                      onChange={e =>
                        this.updateValue(this.state.source, "local", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label className="form-label" htmlFor="validationDefault01">
                      Imported
                    </label>
                    <input
                      type="number"
                      min="0"
                      value={this.state.source.import}
                      onChange={e =>
                        this.updateValue(this.state.source, "import", e)
                      }
                      className="form-input input-lg"
                    />
                  </div>
                </div>
                <h4>Numbers By Region</h4>
                <div className="form-row">
                  <div className="col-md-8 mb-3">
                    <div className="form-group">
                      <select
                        className="form-select"
                        value={this.state.area}
                        onChange={e => {
                          this.setState({
                            area: e.target.value
                          });
                        }}
                      >
                        <option value="none">Choose an Region</option>
                        {Object.keys(localNames.Regions).map((x, y) => (
                          <option key={y} value={x}>
                            {localNames.Regions[x].English}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                </div>
                <div hidden={this.state.area === "none"}>
                  {this.state.area !== "none" ? (
                    <CityInput
                      region={this.state.area}
                      cities={this.formatCities()}
                    ></CityInput>
                  ) : (
                    <div> Select a Region</div>
                  )}
                </div>
                <button className="btn btn-primary" type="submit">
                  Submit New Data
                </button>
              </form>
            </div>
          </div>
        </div>
      );
    else return <div>Loading...</div>;
  }
}
