import React, { Component } from "react";
import "./cityInput.scss";
export default class CityInputs extends Component {
  render() {
    const { cities, region } = this.props;
    return cities.map((val, idx) => {
      let cityId = `city-${idx}`;
      return (
        <div key={idx} className="form-row">
          <div className="col-md-10 mb-3">
            <div className="input-group">
              <span className="input-group-addon addon-lg">
                {cities[idx].name}
              </span>
              <input
                type="number"
                min="0"
                name={cityId}
                data-id={cities[idx].id}
                data-region={region}
                id={cityId}
                value={cities[idx].value}
                onChange={e => {
                  e.preventDefault();
                }}
                className="form-input input-lg city"
                placeholder={cities[idx].name}
              />
            </div>
          </div>
        </div>
      );
    });
  }
}
