import React, { Component } from "react";
import "./dashboard.scss";
import DateRangePicker from "react-bootstrap-daterangepicker";
import moment from "moment";
import LineChart from "../elements/rechart";
import TimeLine from "../elements/timeline";
import BarChart from "../elements/barChart";
import BarChartStack from "../elements/barChartStack";
import PieChart from "../elements/pieChart";
import MapHeatChart from "../elements/mapHeatChart";
import SourceService from "../../services/sourceServices";
import localNames from "../../assets/saudi-arabia/localNames.json";

export default class dashboard extends Component {
  state = {
    intensive: 0,
    hospital: 0,
    recovered: 0,
    deaths: 0,
    total: 0,
    confirmed: 0,
    critical: 0,
    barCases2: [],
    date: {
      start: moment(),
      end: moment()
    },
    nationality: {
      in: 0,
      out: 0
    },
    source: {
      local: 0,
      import: 0
    },
    regionName: "",
    city: {},
    ready: false
  };
  componentDidMount() {
    SourceService.getCases(1).then(data => {
      this.setState({ barCases2: data });
      let vc = data[data.length - 1];
      this.setState({
        total: vc.confirmed + vc.recovered + vc.deaths,
        confirmed: vc.confirmed
      });
      SourceService.getCases(2).then(data => {
        this.setState({
          confirmed: data.confirmed,
          intensive: data.intensive,
          hospital: data.hospital,
          recovered: data.recovered,
          deaths: data.deaths,
          nationality: {
            in: data.nationality.in,
            out: data.nationality.out
          },
          source: {
            local: data.source.local,
            import: data.source.import
          },
          barStats: [],
          city: data.city
        });
        Object.keys(this.state.city).forEach(city => {
          if (Object.keys(this.state.city[city]).length > 0) {
            this.handleHover(city);
          }
        });
        this.setState({ ready: true });
      });
    });
  }
  formatData(chart) {
    let res = [];
    switch (chart) {
      case "pie1":
        res.push({
          name: "Saudi Arabian",
          value: parseInt(this.state.nationality.in)
        });
        res.push({
          name: "Other Nationality",
          value: parseInt(this.state.nationality.out)
        });
        break;
      case "pie2":
        res.push({
          name: "Local",
          value: parseInt(this.state.source.local)
        });
        res.push({
          name: "Imported",
          value: parseInt(this.state.source.import)
        });
        break;
      default:
        return [];
    }
    return res;
  }
  handlePick = (event, picker) => {
    this.setState({
      date: { start: picker.startDate, end: picker.endDate }
    });
  };
  handleHover = area => {
    let region = localNames.Regions[area].Cities;
    let rn = localNames.Regions[area].English;
    let values = this.state.city[area];
    let res = [];
    if (values) {
      Object.keys(values).forEach(value => {
        res.push({ name: region[value].English, cases: values[value] });
      });
    }
    this.setState({ barStats: res, regionName: rn });
  };
  render() {
    if (this.state.ready === true)
      return (
        <div id="dashboard" className="container">
          <div className="columns flex-centered my-2">
            <div className="column col-sm-12 col-9">
              <h5>Coronavirus Outbreak in Saudi Arabia</h5>
            </div>
            <div className="column col-sm-12 col-3">
              <DateRangePicker
                buttonClasses={["btn"]}
                startDate="1/1/2020"
                onEvent={this.handlePick}
              >
                <button className="btn form-input date-wrapper">
                  <span className="date">
                    {this.state.date.start.format("D MMM YYYY")}
                  </span>
                  <span className="date-seperator">→</span>
                  <span className="date">
                    {this.state.date.end.format("D MMM YYYY")}
                  </span>
                </button>
              </DateRangePicker>
            </div>
          </div>
          <div className="number-graph-wrapper">
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>CONFIRMED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-confirmed">
                  <div className="cases-container">
                    <h2 className="case">{this.state.confirmed}</h2>
                    <span className="info">
                      {((this.state.confirmed / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="confirmed"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>HOSPITALIZED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-hospitalized">
                  <div className="cases-container">
                    <h2 className="case">{this.state.hospital}</h2>
                    <span className="info">
                      {((this.state.hospital / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>CASES IN INTENSIVE CARE</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-icu">
                  <div className="cases-container">
                    <h2 className="case">{this.state.intensive}</h2>
                    <span className="info">
                      {((this.state.intensive / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>RECOVERED CASES</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-recovered">
                  <div className="cases-container">
                    <h2 className="case">{this.state.recovered}</h2>
                    <span className="info">
                      {((this.state.recovered / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="recovered"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
            <div className="number-graph mr-2">
              <div className="card">
                <div className="card-header">
                  <div className="card-title ">
                    <h6>
                      <b>DEATHS</b>
                    </h6>
                  </div>
                </div>
                <div className="card-body status-died">
                  <div className="cases-container">
                    <h2 className="case">{this.state.deaths}</h2>
                    <span className="info">
                      {((this.state.deaths / this.state.total) * 100)
                        .toString()
                        .substring(0, 3)}
                      % of total cases
                    </span>
                  </div>
                  <div id="lineChartContainer">
                    <LineChart
                      data={this.state.barCases2}
                      name="deaths"
                    ></LineChart>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-12">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Cumulative Trend</div>
                  <div className="card-subtitle text-gray mt-1">
                    20th Jan to Today
                  </div>
                </div>
                <div className="card-body">
                  <TimeLine data={this.state.barCases2}></TimeLine>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-7">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Map Regions</div>
                  <MapHeatChart
                    total={this.state.total}
                    hover={this.handleHover}
                    cities={this.state.city}
                  ></MapHeatChart>
                </div>
              </div>
            </div>
            <div className="column col-sm-12 col-5">
              <div className="card" id="pie-container">
                <div className="card-container">
                  <div className="card-header">
                    <div className="card-title h6">{this.state.regionName}</div>
                  </div>
                  <div className="card-body">
                    <BarChart data={this.state.barStats}></BarChart>
                  </div>
                </div>
                <div className="columns card-container">
                  <div className="column">
                    <div className="card-header">
                      <div className="card-title h6">Transmission Source</div>
                    </div>
                    <div className="card-body">
                      <PieChart
                        color="1"
                        data={this.formatData("pie2")}
                      ></PieChart>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="columns my-2">
            <div className="column col-sm-12 col-4">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Nationality</div>
                </div>
                <div className="card-body">
                  <PieChart color="2" data={this.formatData("pie1")}></PieChart>
                </div>
              </div>
            </div>
            <div className="column col-sm-12 col-8">
              <div className="card">
                <div className="card-header">
                  <div className="card-title h6">Confirmed COVID-19 Cases</div>
                  <div className="card-subtitle text-gray mt-1">
                    30th Jan to Today
                  </div>
                </div>
                <div className="card-body">
                  <BarChartStack data={this.state.barCases2}></BarChartStack>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    else return <div>Loading ...</div>;
  }
}
